.custom-select-container {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  font-family: Arial;
}

.custom-select-container.is-open .custom-select-opener {
  background: #FFFFFF;
  border: 2px solid #7F7F7F;
}

.custom-select-container.is-disabled {
  opacity: .333;
}

.custom-select-opener {
  font-family: Arial;
  background-color: #fff;
  padding: 18px 20px;
  border-radius: 6px;
  color: #777777;
  display: block;
  cursor: pointer;
  width: 100%;
  height: 50px;
  font-size: 14px;
  line-height: 14px;
}

.custom-select-container select {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.custom-select-panel {
  margin-top: 10px;
  max-height: 0;
  transition: 0.3s;
  overflow: hidden;
  background-color: #e9e9e9;
  position: absolute;
  top: 100%;
  z-index: 1;
  width: 100%;
}

.custom-select-container.is-open .custom-select-panel {
  max-height: 250px;
  overflow-y: auto;
}

.custom-select-option {
  position: relative;
  background: #fff;
  padding: 17px 11px 17px 25px;
  cursor: pointer;
}

.custom-select-option:first-child {
  display: none;
}

.custom-select-option::before {
  content: "";
  width: 7px;
  height: 100%;
  background: #0062FF;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}

.custom-select-option.has-focus::before {
  opacity: 1;
}

.custom-select-option.has-focus,
.custom-select-option.is-selected {
  background: #EDEDED;
}

.custom-select-option.is-selected::before {
  opacity: 1;
}

.custom-select-optgroup > .custom-select-option {
  padding-left: 2em;
}

.custom-select-optgroup::before {
  content: attr(data-label);
  display: block;
  padding: 0.5em;
  color: #888;
}

[data-simplebar] {
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
}

.simplebar-wrapper {
  overflow: hidden;
  width: inherit;
  height: inherit;
  max-width: inherit;
  max-height: inherit;
}

.simplebar-mask {
  direction: inherit;
  position: absolute;
  overflow: hidden;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: auto !important;
  height: auto !important;
  z-index: 0;
}

.simplebar-offset {
  direction: inherit !important;
  box-sizing: inherit !important;
  resize: none !important;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 0;
  margin: 0;
  -webkit-overflow-scrolling: touch;
}

.simplebar-content-wrapper {
  direction: inherit;
  box-sizing: border-box !important;
  position: relative;
  display: block;
  height: 100%;
  /* Required for horizontal native scrollbar to not appear if parent is taller than natural height */
  width: auto;
  max-width: 100%;
  /* Not required for horizontal scroll to trigger */
  max-height: 100%;
  /* Needed for vertical scroll to trigger */
  overflow: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.simplebar-content-wrapper::-webkit-scrollbar,
.simplebar-hide-scrollbar::-webkit-scrollbar {
  display: none;
  width: 0;
  height: 0;
}

.simplebar-content:before,
.simplebar-content:after {
  content: ' ';
  display: table;
}

.simplebar-placeholder {
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  pointer-events: none;
}

.simplebar-height-auto-observer-wrapper {
  box-sizing: inherit !important;
  height: 100%;
  width: 100%;
  max-width: 1px;
  position: relative;
  float: left;
  max-height: 1px;
  overflow: hidden;
  z-index: -1;
  padding: 0;
  margin: 0;
  pointer-events: none;
  flex-grow: inherit;
  flex-shrink: 0;
  flex-basis: 0;
}

.simplebar-height-auto-observer {
  box-sizing: inherit;
  display: block;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 1000%;
  width: 1000%;
  min-height: 1px;
  min-width: 1px;
  overflow: hidden;
  pointer-events: none;
  z-index: -1;
}

.simplebar-track {
  z-index: 1;
  position: absolute;
  right: 0;
  bottom: 0;
  pointer-events: none;
  overflow: hidden;
}

[data-simplebar].simplebar-dragging {
  pointer-events: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

[data-simplebar].simplebar-dragging .simplebar-content {
  pointer-events: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

[data-simplebar].simplebar-dragging .simplebar-track {
  pointer-events: all;
}

.simplebar-scrollbar {
  position: absolute;
  left: 0;
  right: 0;
  min-height: 10px;
}

.simplebar-scrollbar:before {
  position: absolute;
  content: '';
  background: #0062FF;
  border-radius: 7px;
  left: 0px;
  top: 0;
  height: 4px;
  transition: opacity 0.2s 0.5s linear;
}

.simplebar-scrollbar.simplebar-visible:before {
  opacity: 0.5;
  transition-delay: 0s;
  transition-duration: 0s;
}

.simplebar-track.simplebar-vertical {
  top: 0;
  width: 11px;
}

.simplebar-scrollbar:before {
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
}

.simplebar-track.simplebar-horizontal {
  left: 0;
  bottom: -16px;
  height: 6px;
  background: #E6E6E6;
  width: calc(100% - 20px);
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
  right: auto;
  left: 0;
  top: 0;
  bottom: 0;
  min-height: 0;
  min-width: 10px;
  width: auto;
  background: #0062ff;
  border-radius: 20px;
  height: 6px;
}

/* Rtl support */

[data-simplebar-direction='rtl'] .simplebar-track.simplebar-vertical {
  right: auto;
  left: 0;
}

.simplebar-dummy-scrollbar-size {
  direction: rtl;
  position: fixed;
  opacity: 0;
  visibility: hidden;
  height: 500px;
  width: 500px;
  overflow-y: hidden;
  overflow-x: scroll;
  -ms-overflow-style: scrollbar !important;
}

.simplebar-dummy-scrollbar-size > div {
  width: 200%;
  height: 200%;
  margin: 10px 0;
}

.simplebar-hide-scrollbar {
  position: fixed;
  left: 0;
  visibility: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  border: none;
  cursor: pointer;
}

img {
  display: block;
  width: 100%;
}

a {
  text-decoration: none;
}

::placeholder {
  color: #777777;
}

h2 {
  font-weight: 400;
  font-size: 28px;
  line-height: 36px;
  color: #000000;
  font-family: Consolas;
}

.container {
  width: 100%;
  max-width: 778px;
  margin: 0 auto;
  padding: 0 20px;
}

.main {
  padding: 80px 0;
}

.title {
  display: flex;
  align-items: center;
  margin-bottom: 64px;
}

.title::before,
.title::after {
  content: "";
  background: #DBDBDB;
  display: inline-block;
  height: 1px;
  width: 100%;
}

.title h1 {
  font-family: Consolas;
  white-space: nowrap;
  font-weight: 700;
  font-size: 48px;
  line-height: 48px;
  text-align: center;
  color: #0062FF;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin: 0 28px;
}

.banner {
  position: relative;
  padding-bottom: 50px;
  margin-bottom: 55px;
  border-bottom: 1px solid #DCDCDC;
}

.banner__image {
  margin-bottom: 20px;
}

.banner__description {
  font-family: 'Arial';
  font-style: italic;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #202326;
}

.banner-date {
  position: absolute;
  top: 13px;
  left: 20px;
  padding: 24px 29px 22px;
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 25px;
  color: #1A1A1A;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
}

.banner-date__date {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 14px;
}

.banner-date__date svg {
  margin-left: 12px;
}

.banner-date__month {
  font-size: 16px;
  line-height: 25px;
}

.server-js {
  margin-bottom: 45px;
  font-style: italic;
}

.server-js__title {
  margin-bottom: 30px;
}

.server-js__description {
  font-family: 'Open Sans';
  font-size: 18px;
  line-height: 24px;
  color: #1A1A1A;
  display: flex;
}

.server-js__description-wrap > span {
  color: #777777;
  display: block;
  margin-top: 30px;
}

.server-js__description::before {
  content: url("../pic/quotation.png");
  margin-right: 24px;
  transform: translateY(-9px);
}

.book {
  background: #EDEDED;
  padding: 34px 20px 36px;
  display: flex;
  justify-content: center;
}

.book__wrap img {
  height: 200px;
  object-fit: cover;
}

.book__link {
  font-family: 'Helvetica';
  margin-bottom: 17px;
  font-size: 14px;
  line-height: 18px;
}

.conclusion {
  padding: 45px 0 74px;
}

.conclusion__title {
  margin-bottom: 26px;
}

.conclusion__description {
  font-family: 'Arial';
  font-size: 16px;
  line-height: 24px;
  color: #202326;
}

.form {
  padding: 40px 0;
  background: #EDEDED;
}

.form__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: #202326;
  margin-bottom: 10px;
}

.form__comment {
  font-family: Consolas;
  font-weight: 700;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 18px;
  color: #202326;
}

.form__info {
  display: flex;
  margin-bottom: 20px;
}

.form__info .input {
  margin-right: 30px;
}

.form__review {
  width: 100%;
  margin-bottom: 20px;
}

.posts {
  padding-top: 60px;
}

.posts__title {
  margin-bottom: 40px;
}

.posts-list-pagination {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 23px;
}

.posts-list-pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
}

.posts-list-pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  background: #0062FF;
}

.posts-list__item-image {
  margin-bottom: 20px;
}

.posts-list__item-image img {
  max-width: 280px;
}

.posts-list__item-title {
  font-family: Consolas;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  color: #272522;
}

@media (max-width: 767px) {
  input[type=submit] {
    display: block;
    width: 100%;
    max-width: 280px;
    margin: 0 auto;
  }

  .input input {
    width: 100%;
  }

  .stars {
    margin-bottom: 20px;
  }

  h2 {
    font-size: 23px;
    line-height: 36px;
  }

  .main {
    padding: 40px 0 66px;
  }

  .title {
    margin-bottom: 40px;
  }

  .title h1 {
    margin: 0 15px;
    font-size: 23px;
    line-height: 25px;
  }

  .banner {
    display: flex;
    flex-direction: column-reverse;
    padding-bottom: 0;
    margin-bottom: 34px;
    border-bottom: none;
    width: calc(100% + 20px);
  }

  .banner__image img {
    width: 738px;
  }

  .banner__image {
    margin-bottom: 0;
  }

  .banner__description {
    margin-bottom: 26px;
  }

  .banner-date {
    display: none;
  }

  .server-js {
    margin-bottom: 56px;
  }

  .server-js__title {
    margin-bottom: 20px;
  }

  .server-js__description {
    font-size: 14px;
    line-height: 20px;
  }

  .server-js__description::before {
    margin-right: 19px;
  }

  .book {
    padding: 22px 20px 18px;
  }

  .book__link {
    margin-bottom: 16px;
  }

  .conclusion {
    padding: 25px 0 30px;
  }

  .conclusion__title {
    margin-bottom: 21px;
    font-size: 21px;
    line-height: 36px;
  }

  .conclusion__description {
    font-size: 14px;
    line-height: 18px;
  }

  .form__title {
    margin-bottom: 15px;
  }

  .form__comment {
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 10px;
  }

  .form__info {
    flex-direction: column;
    margin-bottom: 10px;
  }

  .form__info .input {
    margin-bottom: 10px;
    margin-right: 0;
  }

  .form__review {
    margin-bottom: 10px;
  }

  .posts {
    padding-top: 36px;
  }
}